*,
*::before,
*::after {
	box-sizing: inherit;
}

html {
	box-sizing: border-box;
	font-family: $font-family-stem;
}

.page-list {
	margin-top: 40px;
	margin-bottom: 40px;
	font-size: 18px;
	thead {
		td {
			font-weight: 700;
			text-transform: uppercase;
		}
	}
	li {
		margin-bottom: 20px;
		&:last-of-type {
			margin-bottom: 0;
		}
	}
	a {
		color: $color-curious-blue;
		text-decoration: none;
	}
	td {
		padding: 20px;
		vertical-align: top;
		border: 1px solid $color-franch-gray;
		border-bottom: none;
		// &:first-of-type {
		// 	text-transform: uppercase;
		// 	font-weight: 700;
		// }
	}
	tr {
		&.question {
			background: $color-aquamarine;
		}

		&.not-ready {
			background: $color-geyser;
		}
		
		&:last-of-type {
			border-bottom: 1px solid $color-franch-gray;
		}
	}
}
// input
.input-unify {
	display: block;
	width: 100%;
	height: 60px;
	margin-bottom: 30px;
	padding: 24px 0 10px 20px;
	box-sizing: border-box;
	border-radius: 5px;
	border: 1px solid $color-mercury;
	color: $color-mine-shaft;
	font-size: 18px;
	line-height: 24px;
	background: $color-alabaster;
	&::placeholder {
		color: $color-mine-shaft;
		font-size: 18px;
	}
}
.textarea-unify {
	display: block;
	width: 100%;
	min-height: 260px;
	margin-bottom: 30px;
	padding: 20px;
	box-sizing: border-box;
	border-radius: 5px;
	border: 1px solid $color-mercury;
	color: $color-mine-shaft;
	font-size: 18px;
	line-height: 30px;
	background: $color-alabaster;
	&::placeholder {
		color: $color-mine-shaft;
		font-size: 18px;
	}
}
.label-unify {
	display: inline-block;
	margin-bottom: 14px;
	padding-left: 10px;
	color: $color-mine-shaft;
	font-size: 14px;
}

// header 
.header {
	position: fixed;
	top: 0;
	right: 0;
	left: 0;
	z-index: 9;
	box-shadow: 0 0 20px rgba(0, 0, 0, 0.25);
	background-color: $color-white;
	&__container {
		display: flex;
		justify-content: space-between;
		align-items: center;
		height: 100px;
	}

	&__logo {
		margin-right: 30px;
		margin-left: 30px;
	}

	&__info {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-right: 90px;
	}

	&__login {
		margin-right: 40px;
	}

	&__name {
		font-size: 24px;
		font-weight: 700;
		color: $color-mine-shaft;
	}

	&__swither {
		font-size: 14px;
		color: $color-curious-blue;
		text-decoration: underline;
	}

	&__notice {
		position: relative;
		margin-right: 5px;
	}

	&__notice-quantity {
		position: absolute;
		top: -9px;
		right: -5px;
		width: 18px;
		height: 18px;
		background: $color-sunset-orange;
		font-size: 12px;
		color: $color-white;
		text-align: center;
		line-height: 18px;
		border-radius: 50%;
	}

}

// aside
.sidebar {
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	z-index: 8;
	width: 250px;
	padding-top: 100px;
	box-shadow: 0 0 4px rgba(0, 0, 0, 0.25);
	background: linear-gradient(to top, #243873, #1b96d1);
	
	&__menu {
		margin-top: 60px;
		margin-bottom: 0;
		padding-left: 0;
		list-style: none;
	}

	&__link {
		display: flex;
		align-items: center;
		height: 80px;
		padding-left: 30px;
		box-sizing: border-box;
		color: $color-periwinkle;
		text-decoration: none;
		cursor: pointer;
		
		.dashboard {
			background: url("../images/dashboard.svg") center no-repeat;			
		
		}
		
		.sponsor {
			background: url("../images/sponsor.svg") center no-repeat;			
		
		}

		.handshake {
			background: url("../images/handshake.svg") center no-repeat;			
		
		}

		.network {
			background: url("../images/network.svg") center no-repeat;			
		
		}

		.dashboard {
			background: url("../images/dashboard.svg") center no-repeat;			
		
		}

		&:hover,
		&.active {
			color: $color-white;
			background: $color-curious-blue;
			
			.dashboard {
				background: url("../images/dashboard_active.svg") center no-repeat;			
			}
			
			.sponsor {
				background: url("../images/sponsor_active.svg") center no-repeat;			
			}
			
			.handshake {
				background: url("../images/handshake_active.svg") center no-repeat;			
			}
			
			.network {
				background: url("../images/network_active.svg") center no-repeat;			
			}
		}
	}
	&__image {
		width: 32px;
		height: 32px;
		margin-right: 15px;
	}
}
.dashboard {
	min-height: calc(100vh - 60px);
	&__container {
		width: 1920px;
		max-width: 100%;
		padding-top: 100px;
		padding-left: 305px;
		padding-right: 35px;
		box-sizing: border-box;
		&_without-sidebar {
			padding-left: 35px;
		}
	}	
	
	&__header {
		width: 100%;
		margin-top: 40px;
		margin-bottom: 40px;
		padding-right: 15px;
		padding-left: 15px;	
	}
	
	&__statistic,
	&__title {
		margin-top: 0;
		margin-bottom: 0;
		color: $color-mine-shaft;
		font-size: 36px;
		font-weight: 700;
	}
	
	&__title {
		display: inline-block;
		margin-right: 30px;
	}

	&__edit {
		display: inline-block;
		color: $color-silver;
		font-size: 14px;
		font-weight: 700;
		text-decoration: none;

		&::before {
			display: inline-block;
			width: 8px;
			height: 8px;
			margin-right: 4px;
			content: '';
			background: url("../images/edit.png") center no-repeat;	
			background-size: contain;	
		}
	}

	&__item {
		min-height: 200px;
		margin-right: 15px;
		margin-bottom: 30px;
		margin-left: 15px;
		box-shadow: 4px 4px 40px rgba(29, 149, 209, 0.1);
		border-radius: 5px;
		background-color: $color-white;

	}

	&__section-header {
		width: 100%;
		margin: 40px 15px;
		color: $color-mine-shaft;
		font-size: 36px;
		font-weight: 700;
	}
}
ul,
h3,
h2,
p {
	margin-top: 0;
	margin-bottom: 0;
}
ul {
	padding-left: 0;
	list-style: none;
}

.dashboard__row {
	display: flex;
	flex-wrap: wrap;
	width: 100%;
	&_first {
		margin-bottom: 30px;
	}

	&_second {
		flex-direction: column;
	}

	&_platform {
		padding-bottom: 50px;
	}

	&.justify {
		justify-content: space-between;
	}

	&.center {
		justify-content: center;
	}
}

.dashboard-brand {
	display: flex;
	align-items: stretch;
	width: 760px;
	&__image {
		display: flex;
		justify-content: center;
		align-items: center;
		min-width: 200px;
		background: $color-alabaster;
	}

	&__content {
		padding: 40px;
	}

	&__header {
		margin-top: 0;
		margin-bottom: 20px;
		color: $color-curious-blue;
		font-size: 36px;
		font-weight: 700;
	}

	&__text {
		margin-top: 0;
		margin-bottom: 0;
		color: $color-mine-shaft;
		font-size: 18px;
	}
}

.dashboard-foundation,
.dashboard-arrangements,
.dashboard-rating {
	padding: 40px 50px;
}

.dashboard-rating {
	width: 180px;
	text-align: center;
	&__header {
		margin-bottom: 15px;
		color: $color-mine-shaft;
		font-size: 18px;
		font-weight: 700;
	}

	&__text {
		color: $color-curious-blue;
		font-size: 48px;
		font-weight: 700;
	}

}

.dashboard-arrangements {
	width: 260px;
	&__header {
		color: $color-franch-gray;
		font-size: 48px;
		font-weight: 700;
	}

	&__text {
		color: $color-gray;
		font-size: 18px;
	}

}

.dashboard-foundation {
	width: 260px;
	&__header {
		color: $color-curious-blue;
		font-size: 48px;
		font-weight: 700;
	}

	&__text {
		color: $color-gray;
		font-size: 18px;
	}
}

.dashboard-financials {
	position: relative;
	width: 760px;
	padding: 40px;
	&__header {
		margin-bottom: 40px;
		padding-right: 30px;
		padding-left: 30px;
		color: $color-mine-shaft;
		font-size: 24px;
		font-weight: 700;
	} 
	
	&__chart {
		margin-bottom: 40px;
		img {
			max-width: 100%;
		}
	}
	
	&__values {
		display: flex;
		justify-content: space-between;
		padding-right: 30px;
		padding-left: 30px;
	}
	
	&__item {
		width: 33.3%;
		text-align: center;
	}

	&__spend {
		margin-bottom: 5px;
		color: $color-mine-shaft;
		font-size: 14px;
		font-weight: 300;
	}

	&__value {
		margin-bottom: 10px;
		color: $color-mine-shaft;
		font-size: 14px;
		font-weight: 700;
	}

	&__spons {
		color: $color-curious-blue;
		font-size: 18px;
		font-weight: 700;
	}

	&__chart-point {
		position: absolute;
		top: 143px;
		left: 288px;
		width: 8px;
		height: 8px;
		border-radius: 50%;
		background: $color-curious-blue;
		&.hidden {
			display: none;
		}
	}
	&__chart-description {
		position: absolute;
		top: 50px;
		left: 288px;
		width: 150px;
		height: 80px;
		padding: 15px 15px 15px  20px;
		box-shadow: 4px 4px 20px rgba(29, 149, 209, 0.1);
		border-radius: 5px;
		background-color: $color-white;
		&.hidden {
			display: none;
		}
	}

	&__chart-value-1 {
		margin-right: 10px;
		color: $color-curious-blue;
		font-size: 24px;
	}
	
	&__chart-value-2 {
		color: $color-sunset-orange;
		font-size: 12px;
	}
	
	&__chart-value-3 {
		color: $color-curious-blue;
		font-size: 14px;
		font-weight: 700;
	}
}

.dashboard-sponsees {
	width: 760px;
	height: 200px;
	padding: 40px 70px;
	&__header {
		margin-bottom: 20px;
		color: $color-mine-shaft;
		font-size: 24px;
		font-weight: 700;
	}
	
	&__list {
		margin-top: 0;
		margin-bottom: 0;
		padding-left: 0;
		font-size: 0;
		list-style: none;
	}

	&__item {
		display: inline-block;
		width: 50%;
		margin-bottom: 15px;
		color: $color-gray;
		font-size: 14px;		
		&::before {
			display: inline-block;
			width: 14px;
			height: 10px;
			margin-right: 10px;
			content: '';
			background: url("../images/check.svg")
		}
	}
}

.dashboard-textbox {
	width: 760px;
	padding: 40px 50px 60px 70px;
	&__header {
		margin-bottom: 24px;
		color: $color-mine-shaft;
		font-size: 24px;
		font-weight: 700;
	}

	&__content {
		color: $color-gray;
		font-size: 14px;
		line-height: 23px;
	}
}

.dashboard-statistic {
	width: 100%;
	margin-bottom: 40px;
	// margin-bottom: 80px;
	padding: 40px 70px;
	&__header {
		margin-bottom: 28px;
		color: $color-mine-shaft;
		font-size: 24px;
		font-weight: 700;
	}

	&__container {
		display: flex;
		flex-wrap: wrap;
	}

	&__item {
		display: flex;
		width: 50%;
		margin-bottom: 40px;
	}

	&__chart {
		width: 210px;
		height: 170px;
		margin-right: 25px;
		text-align: center;
	}
	
	&__title {
		margin-bottom: 18px;
		color: $color-mine-shaft;
		font-size: 18px;
		font-weight: 700;
	}

	&__list {
		padding-left: 56px;
	}

	&__list-item {
		position: relative;
		margin-bottom: 10px;
		color: $color-gray;
		font-size: 14px;
		&::before {
			position: absolute;
			top: 50%;
			left: -15px;
			display: block;
			width: 8px;
			height: 8px;
			margin-top: -4px;
			border-radius: 50%;
			content: '';
		} 
	
		&_curious-blue::before {
			background: $color-curious-blue; 
		}
	
		&_sunset-orange::before {
			background: $color-sunset-orange; 
		}
	
		&_heliotrope::before {
			background: $color-heliotrope; 
		}
	
		&_aquamarine::before {
			background: $color-aquamarine; 
		}
	}
}

.dashboard-platform-section {
	padding-bottom: 50px;
	&__header {
		width: 100%;
		margin-right: 15px;
		margin-top: 40px;
		margin-bottom: 40px;
		margin-left: 15px;
		color: $color-mine-shaft;
		font-size: 36px;
		font-weight: 700;
	}
}

.dashboard-platform {
	display: flex;
	align-items: stretch;
	width: calc(33.33% - 30px);
	padding: 40px 70px;
	&__content {
		margin-right: 70px;
	}

	&__value {
		margin-bottom: 24px;
		color: $color-curious-blue;
		font-size: 48px;
		font-weight: 700;
	}

	&__text {
		color: $color-gray;
		font-family: Stem;
		font-size: 18px;
	}

	&__image {
		display: flex;
		justify-content: center;
		align-items: center;
		min-width: 85px;
	}
}
.dashboard-welcome {
	// width: 760px;
	width: 970px;
	padding: 40px 70px;
	&__title {
		margin-bottom: 20px;
		color: $color-curious-blue;
		font-size: 36px;
		font-weight: 700;
	}
	&__text {
		color: $color-mine-shaft;
		font-size: 18px;
		line-height: 30px;
	}
}
.dashboard-assets {
	width: 100%;
	padding: 60px 70px;
	text-align: center;
	background: linear-gradient(270deg, rgba(36, 56, 115, 1) 0%, rgba(27, 150, 209, 1) 100%);
	&__title {
		margin-bottom: 40px;
		color: $color-white;
		font-size: 48px;
		font-weight: 700;
	}
	
	&__image {
		margin-bottom: 30px;
	}

	&__text {
		margin-bottom: 40px;
		color: $color-white;
		font-size: 18px;
	}

	&__button {
		display: inline-block;
		width: 220px;
		height: 60px;
		border-radius: 5px;
		color: $color-curious-blue;
		font-size: 18px;
		font-weight: 500;
		line-height: 60px;
		background-color: $color-white;
	}
}
.sponsorship-assets {
	width: 450px;
	text-align: center;
	&__image {
		margin-bottom: 20px;
		img {
			width: 100%;
			height: auto;
			max-width: 100%;
		}
	}

	&__title {
		margin-bottom: 10px;
		padding-right: 40px;
		padding-left: 40px;
		color: $color-curious-blue;
		font-size: 36px;
		font-weight: 400;
	}

	&__text-1 {
		color: $color-silver;
		font-size: 14px;
	}

	&__text-2 {
		margin-right: 5px;
		color: $color-curious-blue;
		font-size: 14px;
	}
	
	&__value {
		color: $color-curious-blue;
		font-size: 36px;
		font-weight: 700;
	}	

	&__rating {
		display: flex;
		justify-content: center;
		margin-top: 30px;
		margin-bottom: 40px;
	}

	&__star {
		width: 25px;
		height: 25px;
		margin-right: 10px;
		margin-left: 10px;
		&.full {
			background: url('../images/sponsee/star_full.png') left bottom no-repeat;
		}

		&.half {
			background: 
				url('../images/sponsee/star_half.png') left bottom no-repeat,
				url('../images/sponsee/star_empty.png') left bottom no-repeat;
		}

		&.empty {
			background: url('../images/sponsee/star_empty.png') left bottom no-repeat;
		}
	}

	&__metrics {
		display: flex;
		justify-content: center;
		margin-bottom: 30px;
		padding-right: 40px;
		padding-left: 40px;
	}

	&__metric {
		width: 85px;
		margin-right: 20px;
		margin-left: 20px;
		text-align: left;
	}

	&__metric-value {
		margin-bottom: 10px;
		color: $color-curious-blue;
		font-size: 24px;
		font-weight: 700;
	}

	&__metric-text {
		color: $color-silver;
		font-size: 14px;
		line-height: 23px;
	}

	&__direct {
		display: flex;
	}

	&__button {
		width: 50%;
		color: $color-white;
		font-size: 18px;
		font-weight: 500;
		line-height: 60px;
		&_edit {
			background: $color-geyser;
		}
		
		&_remove {
			background: $color-sunset-orange;
		}
	}
}

.sponsorship-assets-add {
	align-self: center;
	width: 450px;
	height: 450px;
	padding: 60px;
	text-align: center;
	background: linear-gradient(270deg, rgba(36, 56, 115, 1) 0%, rgba(27, 150, 209, 1) 100%);
	&__title {
		margin-bottom: 40px	;
		color: $color-white;
		font-size: 48px;
		font-weight: 700;
	}
}

.return-on {
	width: 1000px;
	padding: 40px 40px 65px 40px;
	&__title {
		margin-bottom: 30px;
		color: $color-mine-shaft;
		font-family: Stem;
		font-size: 24px;
		font-weight: 700;
	}

	&__image {
		margin-right: 150px;
	}

	&__content {
		display: flex;
		align-items: center;
		color: $color-mine-shaft;
		font-size: 14px;
		text-align: center;
	}

	&__description {
		margin-bottom: 5px;
		font-weight: 300;
	}

	&__time {
		margin-bottom: 10px;
		font-weight: 700;
	}

	&__value {
		color: $color-curious-blue;
		font-size: 18px;
		font-weight: 700;
	}
}

.list-new-asset {
	width: 1000px;
	color: $color-mine-shaft;
	font-size: 14px;
	&__container {
		display: flex;
		justify-content: space-between;
		padding: 60px 30px 50px 40px;
		border-bottom: 1px solid $color-concerte;
	}

	&__item {
		&_1 {
			flex-grow: 2;
			margin-right: 30px;
		}
	}

	&__label {
		display: block;
		margin-bottom: 14px;
	}

	&__input {
		display: block;
		width: 100%;
		height: 60px;
		margin-bottom: 30px;
		padding: 24px 0 10px 20px;
		box-sizing: border-box;
		border-radius: 5px;
		border: 1px solid $color-mercury;
		line-height: 24px;
		background: $color-alabaster;
		&::placeholder {
			font-size: 18px;
		}
	}

	&__image-placeholder {
		margin-bottom: 14px;
		text-align: center;
	}

	&__direct {
		display: flex;
		justify-content: flex-end;
		padding: 20px 0;
		&_business {
			width: 100%;
			padding-top: 30px;
			padding-bottom: 80px;
			*:last-child {
				margin-right: 15px;
			}
		}
	}
	&__button {
		width: 200px;
		height: 50px;
		margin-right: 30px;
		padding: 0;
		border: none;
		border-radius: 5px;
		text-align: center;
		line-height: 50px;
		color: $color-white;
		font-size: 18px;
		font-weight: 500;
		&_cancel {
			background: $color-sunset-orange;
		}

		&_confirm {
			background: $color-malachite;
		}
	}
}

.footer {
	position: relative;
	z-index: 9;
	background: $color-gallery;
	text-align: center;
	&__text {
		color: $color-silver;
		font-size: 14px;
		line-height: 60px;
	}
}

.notify-selector {
	cursor: pointer;
}
.notify {
	position: fixed;
	top: 90px;
	right: 50px;
	z-index: 10;
	margin: 0;
	padding: 0 20px 10px 20px;
	opacity: 0;
	visibility: hidden;
	&__item {
		padding-top: 30px;
	}
	
	&__description {
		margin-bottom: 14px;
		color: $color-gray;
		font-size: 14px;
	}
	
	&__title {
		margin-bottom: 20px;
		color: $color-mine-shaft;
		font-size: 18px;
		font-weight: 700;
	}
	
	&__direct {
		display: flex;
	}
	
	&__button {
		width: 120px;
		height: 40px;
		border-radius: 5px;
		color: #ffffff;
		font-size: 18px;
		font-weight: 500;
		line-height: 40px;
		text-align: center;
		cursor: pointer;
		&_decline {
			margin-right: 20px;
			background: $color-sunset-orange;
		}
	
		&_approve {
