		margin-right: auto;
		margin-bottom: 20px;
		margin-left: auto;
		padding: 0;
		border: none;
		border-radius: 5px;
		color: $color-white;
		font-size: 18px;
		font-weight: 500;
		background-color: $color-curious-blue;
	}

	&__account {
		text-align: center;
		&_sponsee {
			color: $color-silver;
		}
	} 

	&__sign-up {
		color: $color-curious-blue;
		text-decoration: none;
	}

	&__radio {
		display: flex;
	}
}

.radio-item {
	width: 50%;
	margin-bottom: 40px;
	&__container {
		display: flex;
		justify-content: flex-end;
		flex-direction: row-reverse;
	}
	
	&__label {
		display: inline-block;
		margin-bottom: 10px;
		margin-left: 8px;
		color: $color-curious-blue;
		font-size: 14px;
		line-height: 18px;
	}
	
	&__input {
		position: relative;
		display: block;
		width: 16px;
		height: 16px;
		margin-top: 1px;
		// margin-left: 3px;
		&::before {
			position: absolute;
			top: 0;
			left: 0;
			z-index: 7;
			display: block;
			width: 16px;
			height: 16px;
			content: '';
			border: 1px solid $color-mercury;
			border-radius: 50%;
			box-sizing: border-box;
			background-color: $color-alabaster;
		}
		&::after {
			position: absolute;
			top: 3px;
			left: 3px;
			z-index: 8;
			display: none;
			width: 10px;
			height: 10px;
			content: '';
			border-radius: 50%;
			background-color: $color-curious-blue;
		}
		&:checked::after {
			display: block;
		}
	}
	
	&__text {
		display: block;
		color: $color-silver;
		font-size: 10px;
	}
}

